export default {
	data() {
		return {
			clipBoardTitles:this.clipBoardTitle,
			clipBoardLinks:this.clipBoardLink,
		};
	},
	props:[
		'clipBoardTitle',
		'clipBoardLink',
	],
	mounted() {},
	watch: {
		clipBoardTitle: {
			handler(a, b) {
				console.log(a, b, '-----------clipBoardTitle----------');
				this.clipBoardTitles = a;
			},
			deep: true
		},
		clipBoardLink: {
			handler(a, b) {
				console.log(a, b, '-----------clipBoardLink----------');
				this.clipBoardLinks = a;
			},
			deep: true
		},
	},
	created() {
		
	},
	methods: {
		clickClipBoard () {
			console.log('复制');


















				uni.setClipboardData({
					data: this.clipBoardLinks,
					success: function () {
					   uni.showToast({
						"title":"复制成功！",
						duration:1000
					   })
					}
				});

		},
	},
};
